//    
.main-nav {
  position: relative;
  width: 100%;
  min-height: 56px;
  display: flex;
  flex-direction: column;
  background-color: @white;

  //              
  @media (min-width: @tablet-width) {
    display: flex;
    background-color: @white;
  }
}

//     
.main-nav__toggle {
  z-index: 3;
  position: absolute;
  display: block;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 22px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  //        
  @media (min-width: @tablet-width) {
    display: none;
  }
}

//     (3    )
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 23px;
  height: 4px;
  background-color: @darkblue;
  //  2-    
  box-shadow: 0 9px 0 0 @darkblue, 0 18px 0 0 @darkblue;
}

//      ()
.main-nav--opened .main-nav__toggle {
  width: 22px;
  height: 22px;

  //  
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 9px;
    right: -2px;
    width: 26px;
    height: 4px;
    background-color: @white;
  }

  //  
  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}

//       
@media (max-width: @mobile-width-only) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
}

//        
//    
.main-nav__list {
  flex-wrap: wrap;
  list-style: none;
  border: none;
  padding: 0;
  margin: 0;

  //          
  @media (min-width: @tablet-width) {
    display: flex;
  }
}

//   
.main-nav__item {
  z-index: 2;
  width: 100%;
  min-height: 55px;
  position: relative;
  background-color: @darkblue;

  //          
  @media (max-width: @mobile-width-only) {
    width: 100%;
    border-top: 1px solid @lighten-blue;
    border-bottom: 1px solid @darken-blue;

    //        
    &:first-child {
      border-top: none;
    }

    //        
    &:last-child {
      border-bottom: none;

      //     
      &::after {
        z-index: 1;
        width: 100%;
        height: 7px;
        position: absolute;
        bottom: 0;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
        content: "";
      }
    }
  }

  //         
  @media (min-width: @tablet-width) {
    z-index: 0;
    width: 20%;

    & {
      background-color: @white;
    }

    //     
    &:nth-child(4n+3) {
      margin-left: 20%;
    }
  }
}

//   
.main-nav__link {
  width: 100%;
  min-height: 55px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
  border: none;
  color: @white;

  //       
  @media (max-width: @mobile-width-only) {
    &:hover {
      background-color: @hover;
    }

    &:active {
      background-color: @active;
      color: rgba(255, 255, 255, 0.3);
    }
  }

  //         
  @media (min-width: @tablet-width) {
    font-size: 14px;
    color: @black;

    //    
    &--current {
      color: @current;
    }

    &:hover {
      color: @darkblue;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

//      +nojs
.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}

.main-nav--nojs .main-nav__list {
  display: block;
}

.main-nav--nojs .main-nav__item {
  display: block;
}

.main-nav--nojs .main-nav__link--current {
  width: 100%;
  min-height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
  border: none;
}
